import React from 'react';
import { IonGrid, IonRow, IonCol, IonImg, IonCard, IonCardHeader, IonCardSubtitle, IonCardTitle, IonCardContent, IonItem, IonIcon, IonLabel, IonButton, IonBadge } from '@ionic/react';

import CardItem from './CardItem';

export interface CardItem {
  title?: string;
  description?: string;
  content?: string;
  imageSrc: string;
  tokens?: number;
};

const CardItemComponent: React.FC<CardItem> = ({title, description, content, imageSrc, tokens}) => {
  return (
    <IonCol size="12" size-sm="12" size-md="6" size-lg="4">
      <IonCard>
        <IonImg src={imageSrc} className="card-image" />
        <IonCardHeader>
          {title ? <IonCardSubtitle>{title}</IonCardSubtitle> : []}
          {tokens ? <IonBadge className="my-1">{tokens} tokens</IonBadge> : [] }
          {description ? <IonCardTitle>{description}</IonCardTitle> : [] }
        </IonCardHeader>
        {content ? 
        <IonCardContent>
          {content}
        </IonCardContent> : [] }
      </IonCard>
    </IonCol>
  )
};

export default CardItemComponent;